.contact form .tg {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
}
.contact form .tg {
    grid-column: 1 / 3;
}
.contact form .tg1 {
    grid-column: 1 / 1;
}
.contact form .tg2 {
    grid-column: 2 / 2;
}.contact form .tg3 {
    grid-column: 3 / 3;
}