
.containerss {
    display: flex;
    height: 75vh !important;
}
.sidebar {
    width: 300px;
    min-width: 300px;
    display: flex;
    background: #fff;
    flex-direction: column;
    border: 1px solid #ccc;
    transition: 500ms all;
    border-radius: 20px;
}
.sidebar .logo {
    display: flex;
    margin: 10px 0 0 0;
    padding-bottom: 10px;
    align-items: center;
    justify-content: center;
    color: #000;
    font-size: 3em;
    letter-spacing: 7px;
    border-bottom: 1px solid #ccc;
}
.sidebar .list-wrap {
    width: 100%;
    overflow: auto;
}
.sidebar .list-wrap .list {
    border-bottom: 1px solid #ccc;
    background: #fff;
    display: flex;
    align-items: center;
    padding: 5px;
    height: 70px;
    cursor: pointer;
}
.sidebar .list-wrap .list:hover, sidebar .list-wrap .list.active {
    background: #f4f7f9;
}
.sidebar .list-wrap .list img {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    object-fit: cover;
    margin-right: 10px;
    box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
}
.sidebar .list-wrap .list .info {
    flex: 1;
}
.sidebar .list-wrap .list .info .user {
    font-weight: 700;
}
.sidebar .list-wrap .list .info .text {
    display: flex;
    margin-top: 3px;
    font-size: 0.85em;
}
.sidebar .list-wrap .list .time {
    margin-right: 5px;
    margin-left: 5px;
    font-size: 0.75em;
    color: #a9a9a9;
}
.sidebar .list-wrap .list .count {
    font-size: 0.75em;
    background: #bde2f7;
    box-shadow: 0 5px 15px -5px rgba(0, 0, 0, .7);
    padding: 3px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    text-align: center;
    color: #000;
}
.content {
    flex: 1;
    display: flex;
    flex-direction: column;
    background: white;
    border: 1px solid #ccc;
    border-radius: 20px;
    padding: 0;
}
.content header {
    height: 76px;
    background: #fff;
    border: 1px solid #ccc;
    display: flex;
    padding: 10px;
    align-items: center;
    border-radius: 20px;
}
.content header img {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    object-fit: cover;
    margin-right: 10px;
    box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
}
.content header .info {
    flex: 1;
}
.content header .info .user {
    font-weight: 700;
}
.content header .info .time {
    display: flex;
    margin-top: 3px;
    font-size: 0.85em;
}
.content header .open {
    display: none;
}
.content header .open a {
    color: #000;
    letter-spacing: 3px;
}
.message-wrap {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 15px;
    overflow: auto;
}
.message-wrap::before {
    content: '';
    margin-bottom: auto;
}
.message-wrap .message-list {
    align-self: flex-start;
    max-width: 70%;
}
.message-wrap .message-list.me {
    align-self: flex-end;
}
.message-wrap .message-list.me .msg {
    background: #bde2f7;
    color: #111;
}
.message-wrap .message-list .msg {
    background: #fff;
    box-shadow: 0 5px 15px -5px rgba(0, 0, 0, .1);
    padding: 10px 5px;
    margin-bottom: 10px;
    border-radius: 5px;
}
.message-wrap .message-list .time {
    text-align: right;
    color: #999;
    font-size: 0.75em;
}
.message-footer {
    border: 1px solid #ddd;
    background: #eee;
    padding: 10px;
    display: flex;
    height: 60px;
    border-radius:20px;
}
.message-footer input {
    flex: 1;
    padding: 0 20px;
    border-radius: 5px;
}
@media only screen and (max-width: 480px), only screen and (max-width: 767px) {
    .sidebar {
        position: absolute;
        width: 100%;
        min-width: 100%;
        height: 0vh;
        bottom: 0;
        box-shadow: 0 5px 25px -5px rgba(0, 0, 0, 1);
   }
    .sidebar.opened {
        height: 70vh !important;
   }
    .sidebar .logo {
        display: none;
   }
    .sidebar .list-wrap .list .count {
        font-size: 0.75em;
   }
    .header .open {
        display: block !important;
   }
}
